<template>
    <BaseModal
        name="playlist-kebab"
        :mobileMode="true"
        :closeOutside="true"
        :isLoading="data.loading"
    >
        <template v-slot:title>
            <div class="title">
                <div class="img" @click="gallery">
                    <Img v-if="data.item?.cover?.resized"
                        :preview="data.item?.cover?.resized"
                        :original="data.item?.cover?.original"
                        :alt="locale.title(data.item?.title)"
                        aspectRatio="1 / 1"
                    />
                </div>
                <span>{{ locale.title(data.item?.title) }}</span>
            </div>
        </template>
        <template v-slot:content>
            <ul class="kebab-list">
                <li class="clickable" @click="shareIt">
                    <Icon icon="share"/>
                    <span>{{ t('modals.playlist-kebab.share') }}</span>
                </li>
            </ul>
        </template>
    </BaseModal>
</template>

<script setup>
import { onMounted, reactive } from 'vue';
import { useI18n } from 'vue-i18n';

import Img from 'ðŸ”—/components/ui/Img.vue';
import BaseModal from 'ðŸ”—/components/modals/BaseModal.vue';
import stores from '@/stores';

import api from 'ðŸ”—/api';
import { message } from 'ðŸ”—/utils/utils';

const { t } = useI18n();

const tg = stores.tg();
const modals = stores.modals();
const share = stores.share();
const locale = stores.locale();

const data = reactive({
    loading: true
});


const shareIt = () => {
    close();

    share.playlist(data.item);
}

const gallery = () => {
    if (!data.item?.id || !data.item?.cover?.original) {
        return;
    }

    modals.open('gallery', {
        quietClose: true,
        data: { imgs: [{
            title: locale.title(data.item?.title),
            ...data.item?.cover
        }] } });
}

const like = () => {
    message.info(t('common.soon'));
}

const close = () => {
    modals.close('playlist-kebab');
};


onMounted(async () => {
    const payload = modals.getData('playlist-kebab');

    if (!payload?.id) {
        close();
        return;
    }

    if (payload.item) {
        data.item = payload.item;
    } else {
        data.item = await api.playlists().get(payload.id);
    }

    data.loading = false;
});

</script>

<style lang="scss" scoped>
    .title {
        display: flex;
        width: 100%;
        align-items: center;
        gap: 24px;
        flex-direction: column;
        padding-top: 12px;

        .img {
            width: 74px;
            height: 74px;
            border-radius: 12px;
            @include animage-booble-shadow;

            img {
                width: 100%;
                height: 100%;
                border-radius: 12px;
            }
        }
    }

    ul.kebab-list {
        @include settings-list;
        margin-top: 24px;
    }
</style>
